body {
  background-color: rgb(252, 252, 252);
}

a, a:visited, a:hover, a:active {
  color: inherit;
  text-decoration: none;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space-between {
  justify-content: space-between;
}

.flex-column {
  flex-direction: column;
}

/*      NAVBAR      */

.navbar {
  border-bottom: 1px solid black;
}

.hotbar {
  border-bottom: 1px solid rgb(138, 138, 138);
  background-color: rgb(238, 238, 238);
}

.hotbar ul {
  list-style: none;
  font-size: 0.7rem;
  margin-left: 50px;
  padding: 3px 0;
}

.hotbar-popular {
  color: rgb(230, 79, 19);
  font-weight: bold;
}

.hotbar a:hover {
  text-decoration: underline;
}

.hotbar a,
.hotbar h6,
.nav a {
  margin-right: 7px;
}

.nav {
  height: 50px;
  align-items: flex-end;
  background-color: #add6e9;
}

.nav-list {
  align-items: flex-end;
}

.nav h1 {
  font-size: 2.1rem;
  margin: 0 30px;
  font-weight: bold;
}

.nav li {
  margin-right: 5px;
  margin-bottom: 1px;
}

.nav li a {
  background-color: #ebf1f5;
  color: #374b53;
  padding: 1px 7px;
  font-weight: bold;
}

/*      BODY       */

.main-body {
  width: 90%;
  max-width: 1400px;
  margin: 20px auto 0;
}

.signup-box {
  float: right;
}

.topic {
  margin-bottom: 10px;
}

.topic-item {
  margin-right: 15px;
  min-width: 30px;
  max-width: 30px;
  color: rgb(148, 148, 148);
}

.topic-votes {
  font-weight: bold;
}

.topic-thumbnail {
  min-width: 80px;
  max-width: 80px;
  min-height: 70px;
}

.topic-thumbnail-default {
  background: url('https://www.redditstatic.com/sprite-reddit.5kxTB7FXse0.png') 0 -510px;
  background-size: 100%;
}

.topic-thumbnail img {
  width: 70px;
  height: 70px;
}

.info-title {
  color: blue;
  font-size: 1.2rem;
}

.info-additional {
  font-size: 0.8rem;
  color: rgb(148, 148, 148);
}

.info-additional div {
  margin-right: 10px;
}

.info-additional a {
  color: rgb(88, 88, 184);
}

.topic-info-item {
  margin-bottom: 3px;
}

.info-share {
  color: rgb(104, 104, 104);
  font-weight: bold;
  font-size: 0.8rem;
  margin-top: 4px;
}

.info-share:hover {
  text-decoration: underline;
}